/*dark blue: 29A0DA*/
/*light blue: EEF1F4*/

body{
    margin:0px;
}

p{
    font-size: 1.2em;
}

.bezel-area{
    width: 100%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    display: inline-block;
    background-color: white; 
    border-top: 1px solid black;
    box-shadow: 0px -4px 6px rgba(0, 0, 0, 0.25);
    border-top-left-radius: 27px;
    border-top-right-radius: 27px;
}

.bluebox{
    background-color: #29A0DA;
    width: 30%;
    height: 10px;
    margin: 0 auto;
    margin-top: 1em;
    border-radius: 10px;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);

}

.location-text{
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
}

.location-text p{
    font-size: 1.5em;
    font-weight: bold;
}

/*Button Area*/

.flex-container{
    width: 100%;
    height: 30%;
    display: flex;
    justify-content: space-evenly;
    background-color: #EEF1F4;
    box-shadow: 0px -3px 4px rgba(0, 0, 0, 0.25);
}
.button{
    color: black;
    padding: 1.5em 1.5em 0em 1.5em;
    margin: 1em 1em 1em 1em;
    border: none;
}

.button:hover{
    background-color: #29A0DA;
    color: white;
    border-radius: 25px;    
}
.filter-button{
    background-color: #29A0DA;
    color: white;
    padding: 1em;
    border-radius: 50%;
    z-index: 2;
    border: none;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);

}
/* General style */
body{
    background-color: #F2F2F2;
    
}

button{
    border: none;
}

div {
    font-family: 'Inter', sans-serif;
}
a {
    text-decoration: none;
}

.blue-background{
    background-color: #29A0DA;
    width: 100%;
    height: 50%;
    position: fixed;
    left: 0px;
    top: -23px;
    border-radius: 15px;
    z-index: -1;
}

.extra-margin {
    margin-top: 100px;
}
.container-form {
    width: 358px;
    background-color: white;
    margin: 0 auto;
    box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.14), 0px 2px 1px rgba(0, 0, 0, 0.12), 0px 1px 3px rgba(0, 0, 0, 0.2);
    border-radius: 10px;
    text-align: center;

}

/* Login landing*/
.login-landing-bg {
    background-color: #29A0DA;
}

#logo-landing {
    width: 175px;
    height: 175px;
    display: block;
    margin-left: auto;
    margin-right: auto;
}

/* Login form */
.login-form {
    height: 329px;
    position: relative;
    padding-top: 71px;
}
#logo-form {
    width: 123px;
    height: 123px;
    display: block;
    margin-left: auto;
    margin-right: auto;
    position: absolute;
    left: 118px;
    top: -61px;
}
#login-text {
    color: #29A0DA;
    width: 296px;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    margin: 0 auto;
}
.btn {
    font-size: 18px;
    font-weight: 700;
    line-height: 22px;
    display: inline-block;
    border-radius: 10px;
}
.btn-signin {
    margin-top: 41px;
    padding: 19px 134px;
    background-color: #29A0DA;
    color: #fff;
}
#btn-signup {
    color: #29A0DA;
    border: 1px solid #29A0DA;
    padding: 19px 130px;
}
#text-or {
    display: inline-block;
    padding-left: 16px;
    padding-right: 16px;
    color: rgba(0, 0, 0, 0.6);
}
.line {
    display: inline-block;
    background-color: rgba(0, 0, 0, 0.6);
    width: 141px;
    height: 1px;
    margin-bottom: 4px;
}

/* Signin form */
#logo-small {
    width: 87px;
    height: 87px;
    display: block;
    margin-left: auto;
    margin-right: auto;
}
.title {
    font-weight: 700;
    font-size: 18px;
    line-height: 22px;
    text-align: center;
    margin-top: 0;
    color: white;
}
.signin-form {
    height: 347px;
    padding-top: 36px;
    margin-bottom: 46px;
}
.form-input {
    width: 312px;
    height: 50px;
    background-color: #DBE9F5;
    border-radius: 5px;
    border: none;
    padding-left: 16px;
    padding-right: 0;
}
.form-input::-webkit-input-placeholder {
