    line-height: 19px;
}
.form-input:-ms-input-placeholder {
    color: #939393;
    font-family: 'Inter', sans-serif;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
}
.form-input::placeholder {
    color: #939393;
    font-family: 'Inter', sans-serif;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
}
.form-input#password-signin {
    margin-top: 34px;
}
#forgot-pass {
    font-weight: 600;
    font-size: 16px;
    line-height: 19px;
    color: #29A0DA;
    display: inline-block;
    width: 93%;
